.Select {
    margin-bottom: 20px;
}

.Select label {
    margin: 0 0 3px 0;
    padding: 0;
    font-weight: bold;
    display: block;
}

.Select select {
    display: block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #bebebe;
    margin: 0 0 5px;
    height: 30px;
    width: 100%;
    outline: none;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}


